import React, { useEffect, useState } from 'react';
import {
  FormControl,
  FormControlLabel,
  FormLabel,
  Radio,
  RadioGroup,
  Grid,
  Box,
  CssBaseline,
} from "@material-ui/core";
import { createTheme, ThemeProvider } from "@material-ui/core/styles";

declare global {
  interface Window {
      chrome: any;
      matchMedia: (query: string) => MediaQueryList;
  }
}

type ChromeSetting = {
  selectBehaviour?: string;
  theme?: string;
}

const chrome = window.chrome;
const matchMedia = window.matchMedia;

const selectBehaviourOptions = [
  {
    label: "Checkbox",
    value: "single-check"
  },
  {
    label: "Double Click",
    value: "double-click"
  },
  {
    label: "Both",
    value: "both"
  }
];

const themeOptions = [
  {
    label: "Dark",
    value: "dark",
  },
  {
    label: "Light",
    value: "light",
  },
  {
    label: "Default",
    value: "default"
  }
];

function App() {
  const defaultSetting : ChromeSetting = {
    selectBehaviour: 'both',
    theme: 'default'
  }

  const [keyValue, setKeyValue] = useState(defaultSetting);

  useEffect(() => {
    chrome.storage.local.get(defaultSetting)
      .then( (cs : ChromeSetting) => setKeyValue(cs))
  }, [])

  const onRadioChange = (e: any) => {
    let updateOpt : {[index: string] : any} = {}
    updateOpt[e.target.name] = e.target.value
    chrome.storage.local.set(updateOpt)

    setKeyValue(values => ({
      ...values,
      [e.target.name]: e.target.value 
    }))
  }

  // const onCheckboxChange = (e: any) => {
  //   let updateOpt : {[index: string] : any} = {}
  //   updateOpt[e.target.name] = e.target.checked
  //   chrome.storage.local.set(updateOpt)

  //   setKeyValue(values => ({
  //     ...values,
  //     [e.target.name]: e.target.checked 
  //   }))
  // }

  const generateRadioOptions = (options: any[]) => {
    return options.map((singleOption) => (
      <FormControlLabel
        value={singleOption.value}
        label={singleOption.label}
        control={<Radio />}
      />
    ));
  };

  const darkTheme = createTheme({
    palette: {
      type: keyValue.theme === 'dark' || (keyValue.theme === 'default' && matchMedia('(prefers-color-scheme: dark)').matches) ? 'dark' : 'light',
    },
  });

  return (
    <ThemeProvider theme={darkTheme}>
      <CssBaseline />
      <Box p={4}>
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <FormControl component="fieldset">
              <FormLabel component="legend">How to select</FormLabel>
              <RadioGroup name="selectBehaviour" value={keyValue.selectBehaviour} onChange={onRadioChange} row={true}>
                {generateRadioOptions(selectBehaviourOptions)}
              </RadioGroup>
            </FormControl>
          </Grid>
          <Grid item xs={12}>
            <FormControl component="fieldset">
              <FormLabel component="legend">Theme</FormLabel>
              <RadioGroup name="theme" value={keyValue.theme} onChange={onRadioChange} row={true}>
                {generateRadioOptions(themeOptions)}
              </RadioGroup>
            </FormControl>
          </Grid>
        </Grid>
      </Box>
    </ThemeProvider>
  );
}

export default App;
